<template>
    <div class="activity" v-loading="isLoading">
        <template v-if="item">
            <el-row>
                <h3>{{ item.title }}</h3>
                {{ campuses[item.campus] }}æ ¡åŒº / {{ item.site }}
                <el-progress :percentage="totalPercentage" :status="progressStatus">
                    å·²æŠ¥å {{ totalCount }} / {{ item.maxNeed }}
                </el-progress>
                <div class="activity-desc left" v-html="item.desc" />
            </el-row>
            <el-row>
                <el-timeline class="left">
                    <el-timeline-item :timestamp="dueString">æŠ¥åæˆªæ­¢</el-timeline-item>
                    <el-timeline-item v-for="(v, i) in item.periods" :key="i"
                        :timestamp="periodString[i]" :color="timelineColor[timelineData[i]]">
                        {{ v.name ? v.name : `æ—¶æ®µ ${ i + 1 }` }}
                        <el-tag :type="timelineStatus[timelineData[i]]" size="small">
                            {{ timelineHint[timelineData[i]] }}
                        </el-tag>
                        <el-progress :percentage="percentages[i]">
                            å·²æŠ¥å {{ counts[i] }} / {{ item.periods[i].need }}
                        </el-progress>
                    </el-timeline-item>
                </el-timeline>
            </el-row>
            <el-row>
                <template v-if="canSign">
                    <el-button v-if="signStatus" type="info" @click="cancel">å–æ¶ˆæŠ¥å</el-button>
                    <el-button v-else type="primary" @click="isSignDialogOpen = true">æŠ¥å</el-button>
                </template>
                <el-button v-if="isPoster" @click="edit">ç¼–è¾‘</el-button>
            </el-row>
            <el-row>
                <h3>æŠ¥åè€…åˆ—è¡¨</h3>
                <el-table style="width: 100%"
                    v-if="users.length" :data="users">
                    <el-table-column prop="username" label="ç”¨æˆ·å" width="150" />
                    <el-table-column prop="joined" label="æŠ¥åæ—¶æ®µ" width="150" />
                    <el-table-column prop="createdAt" label="æŠ¥åæ—¶é—´" width="200" />
                    <el-table-column v-if="isPoster" label="æ“ä½œ" fixed="right" width="100"><template #default="scope">
                        <el-button type="danger" @click="deleteRecord(scope)">åˆ é™¤</el-button>
                    </template></el-table-column>
                </el-table>
            </el-row>
        </template>
        <el-empty v-else description="èµ°é”™è·¯äº†ğŸ˜¡" />
    </div>
    <el-dialog v-model="isSignDialogOpen" title="æŠ¥å">
        <span>å·²ç»è‡ªåŠ¨ä¸ºæ‚¨é€‰æ‹©äº†ä¸æ—¥ç¨‹ä¸å†²çªçš„è‹¥å¹²æ—¶æ®µï¼Œæ‚¨å¯ä»¥æ‰‹åŠ¨è°ƒæ•´ã€‚æ³¨æ„ï¼šé¢œè‰²ä¸º<span class="warning">é»„è‰²</span>çš„æ—¶æ®µä¸æ‚¨çš„æ—¥ç¨‹å­˜åœ¨å†²çªï¼Œè¯·æ…é‡é€‰æ‹©ã€‚</span>
        <span><el-checkbox-group v-model="checkedPeriods">
            <el-checkbox v-for="(v, i) in item.periods" :key="i" :label="i"
                :class="{ warning: timelineData[i] == 3}" :disabled="timelineData[i] == 0">
                {{ v.name ? v.name : `æ—¶æ®µ ${ i + 1 }` }}
            </el-checkbox>
        </el-checkbox-group></span>
        <template #footer><span class="dialog-footer">
            <el-button @click="isSignDialogOpen = false">å–æ¶ˆ</el-button>
            <el-button type="primary" :disabled="checkedPeriods.length == 0"
                @click="signIn">æŠ¥å</el-button>
        </span></template>
    </el-dialog>
</template>

<script>
import AV from 'leancloud-storage';
let dayjs = require('dayjs')
export default {
    name: 'Activity',
    data () {
        return {
            user: AV.User.current(),
            isLoading: true,
            isPoster: false,
            activity: undefined,
            item: undefined,     // activity.toJSON()
            record: undefined,   // UserActivityMap è¡¨ä¸­çš„è®°å½•
            records: [],
            totalCount: 0,       // æŠ¥åäººæ•°
            counts: [],
            percentages: [],
            users: [],           // æŠ¥åè€…åˆ—è¡¨
            campuses: ['è¥¿åœŸåŸ', 'æ²™æ²³', 'å®ç¦'],
            timelineData: [],
            timelineStatus: ['danger', 'success', '', 'warning', 'info'],
            timelineHint: ['å·²æˆªæ­¢', 'å·²æŠ¥å', 'å¯æŠ¥å', 'æœ‰å†²çª', 'å·²æŠ¥æ»¡'],
            timelineColor: ['#F56C6C', '#67C23A', '#409EFF', '#E6A23C', '#909399'],
            dueString: '',
            periodString: [],
            isSignDialogOpen: false,
            checkedPeriods: []
        }
    },
    computed: {
        totalPercentage () { return Math.floor(this.totalCount / this.item.maxNeed * 100) },
        canSign () { return dayjs().isBefore(dayjs(this.item.due)) },
        progressStatus () {
            if (!this.canSign) return 'warning'
            if (this.totalPercentage == 100) return 'success'
            return ''
        },
        signStatus () { return this.record !== undefined }
    },
    beforeMount () {
        this.isLoading = true
        // è·å–æ´»åŠ¨ä¿¡æ¯
        const activityQuery = new AV.Query('Activity')
        activityQuery.get(this.$route.params.id).then(activity => {
            this.item = activity.toJSON()
            this.activity = activity
            this.isPoster = (this.item.from?.objectId == this.user.id)

            const userRecordQuery = new AV.Query('UserActivityMap')
            userRecordQuery.equalTo('user', this.user)
            return userRecordQuery.find()
        }).then(res => {
            for (let i in res)
                if (res[i].get('activity').id == this.item.objectId)
                    this.record = res[i]
            this.records = res

            // è·å–æŠ¥åäººæ•°
            const recordsQuery = new AV.Query('UserActivityMap')
            recordsQuery.equalTo('activity', this.activity)
            return recordsQuery.findAndCount()
        }).then(res => {
            // this.totalCount = Number(res[1])
            for (let i = 0; i < this.item.periods.length; i ++)
                this.counts.push(0)
            for (let i in res[0]) {
                res[0][i].get('joined').forEach(j => this.counts[j] += 1)
                this.totalCount += res[0][i].get('joined').length
                let json = res[0][i].toJSON()
                json.createdAt = dayjs(json.createdAt).format('YYYY-MM-DD HH:mm')
                this.users.push(json)
            }
            for (let i in this.item.periods)
                this.percentages.push(Math.floor(this.counts[i] / this.item.periods[i].need * 100))
            
            let date2HHmm = date => Number(dayjs(date).format('HHmm'))
            let array2HHmm = arr => Number(`${arr[0]}${arr[1]}`)
            let todoSchedule = this.user.get('todo')

            this.dueString = dayjs(this.item.due).format('YYYY-MM-DD HH:mm')
            this.periodString = this.item.periods?.map(v => `
                ${dayjs(v.date).format('YYYY-MM-DD')}
                ${dayjs(v.time[0]).format('HH:mm')} -
                ${dayjs(v.time[1]).format('HH:mm')}
            `)
            this.timelineData = this.item.periods?.map((v, i) => {
                if (v.need == this.counts[i]) return 4
                if (!this.canSign) return 0

                let start2 = date2HHmm(v.time[0])
                let end2 = date2HHmm(v.time[1])

                let weekSchedule = this.user.get('schedule')[dayjs(v.date).day()]
                for (let i in weekSchedule) {
                    let period = weekSchedule[i]
                    let start1 = array2HHmm(period[0])
                    let end1 = array2HHmm(period[1])
                    if (start1 < end2 && start2 < end1) return 3
                }
                
                for (let i in todoSchedule) {
                    let todo = todoSchedule[i]
                    if (!dayjs(todo.date).isSame(dayjs(v.date), 'day')) continue
                    let start1 = date2HHmm(todo.time[0])
                    let end1 = date2HHmm(todo.time[1])
                    if (start1 < end2 && start2 < end1) return 3
                }

                for (let i in this.records) {
                    let act = this.records[i].toJSON()
                    for (let j in act.joined) {
                        let period = act.periods[act.joined[j]]
                        if (!dayjs(period.date).isSame(dayjs(v.date), 'day')) continue
                        let start1 = date2HHmm(period.time[0])
                        let end1 = date2HHmm(period.time[1])
                        if (start1 < end2 && start2 < end1) return 3
                    }
                }

                if (this.checkedPeriods.length < 3)
                    this.checkedPeriods.push(i)
                return 2
            })
            this.record?.get('joined').forEach(i => this.timelineData[i] = 1)

            this.isLoading = false
        })
    },
    methods: {
        edit () { this.$router.push('/edit/' + this.$route.params.id) },
        signIn () {
            this.isSignDialogOpen = false
            this.isLoading = true
            let record = new AV.Object('UserActivityMap')
            // å¿…è¦ä¿¡æ¯
            record.set('user', this.user)
            record.set('activity', this.activity)
            record.set('periods', this.item.periods)
            record.set('joined', this.checkedPeriods)
            // å†—ä½™é¡¹
            record.set('username', this.user.getUsername())
            record.set('title', this.item.title)
            record.set('campus', this.item.campus)
            record.set('site', this.item.site)
            record.save().then(() => {
                this.$root.refresh()
            })
        },
        cancel () {
            this.record.destroy().then(() => {
                this.$root.refresh()
            })
        }
    }
}
</script>

<style scoped>
.el-progress {
    margin-top: 10px;
}

.activity-desc {
    margin-top: 10px;
    line-height: 1.5;
}

.el-row {
    margin-bottom: 20px;
    display: block;
}

.left {
    text-align: left;
}

.warning {
    color: #E6A23C;
}
</style>